@font-face {
  font-family: "Lato_bold";
  src: local(Lato), 
  url(/fonts/Lato-Bold.woff2) format("woff2"), 
  url(/fonts/Lato-Bold.woff) format("woff"), 
}
@font-face {
  font-family: "Lato";
  src: local(Lato), 
  url(/fonts/Lato-Regular.woff2) format("woff2"), 
  url(/fonts/Lato-Regular.woff) format("woff"), 
}


body {
    margin: 0;
    padding: 8px;
    font-family: "Lato";
    font-style: normal;
    font-weight: normal;
    a {
        text-decoration: none;
    }
    ul {
        margin: 0;
        padding: 0;
        li {
            list-style: none;
        }
    }
    h1, h2, h3, h4, h5, h6, p {
        margin: 0;
    }
    h1 {
        padding-bottom: 20px;
        padding-top: 20px;
        font-size: 45px;
        line-height: 40px;
        color: #272343;
    }
    .wrapper {
        max-width: 100%;
        margin: auto;
        .list_headings {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
            padding-bottom: 16px;
            h2 {
                font-size: 20px;
                font-weight: 600;
                padding: 8px;
                border: 1px solid blue;
                border-radius: 6px;
            }
        }
        .container {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            color: white;
            background-color: black;
            padding: 30px 10px;
            margin-bottom: 16px;
            font-size: 20px;
            .arrow {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
            }
        }
        .form_container {
            max-width: 50%;
            margin: auto;
            border-radius: 16px;
            padding: 20px 30px;
            background-color: white;
            box-shadow: 1px 1px 5px black;
            display: flex;
            flex-direction: column;
            font-size: 20px;
            h2 {
                padding-bottom: 10px;
            }
            input, select {
                border: 1px solid gray;
                border-radius: 4px;
                padding: 10px 20px;
                font-size: 16px;
                margin-bottom: 10px;
                outline: none;
                cursor: pointer;
            }
            input:hover, select:hover, textarea:hover {
                box-shadow: 1px 1px 5px blue;
            }
            .form_lyrics {
                margin-bottom: 10px;
                outline: none;
                cursor: pointer;
                border-radius: 4px;
            }
            .form_button {
                background-color: white;
                color: black;
                font-weight: 700;
                border: 1px solid black;
                box-shadow: 1px 1px 5px blue;
                border-radius: 8px;
                padding: 10px 30px;
                font-size: 16px;
                outline: none;
                border: none;
            }
            .form_button:hover {
                background-color: green;
                color: yellow;
            }
        }
        .style {
            h2 {
                font-size: 40px;
                line-height: 35px;
                color: #000000;
                padding-bottom: 20px;
                img {
                    width: 40px;
                }
            }
            .styles {
                padding: 20px;
                background: #272343;
                border-radius: 5px;
                color: #FFFFFE;
                margin-bottom: 16px;
                max-width: 50%;
                margin-right: auto;
                h3 {
                    font-size: 30px;
                    line-height: 44px;
                    margin-bottom: 4px;
                }
                p {
                    font-size: 18px;
                    line-height: 22px;
                }
            }
        }
        .cart_songs {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 32px;
            color: white;
            background: #272343;
            border-radius: 5px;
            margin-bottom: 16px;
            button.delete {
                border: none;
                padding: 10px 20px;
                border-radius: 4px;
                font-size: 16px;
                outline: none;
                text-transform: uppercase;
            }
            p {
                color: #FFD803;
            }
        }
    }
}

@media(min-width: 720px) {
    body {
        .wrapper {
            max-width: 700px;
            margin: auto;
        }
    }
}